	module.exports = {"data":{"markdownRemark":{"html":"<p>On utilise le debut de la conjugaison a la troisieme personne du pluriel (ILS) pour construire le subjonctif des verbes. Il y a deux exceptions: NOUS et VOUS, qui utilisent eux memes.</p>\n<!--more-->\n<h2>Premier groupe</h2>\n<p>Radical de l'indicatif present + e, es, e, ions, iez, ent.</p>\n<h3>Prendre</h3>\n<ul>\n<li>Que je prenne</li>\n<li>Que tu prennes</li>\n<li>Qu'il / elle prenne</li>\n<li>Que nous prenions</li>\n<li>Que vous preniez</li>\n<li>Qu'ils / elles prennent</li>\n</ul>\n<h3>Marcher</h3>\n<ul>\n<li>Que je marche</li>\n<li>Que tu marches</li>\n<li>Qu'il / elle marche</li>\n<li>Que nous marchions</li>\n<li>Que vous marchiez</li>\n<li>Qu'ils / elles marchent</li>\n</ul>\n<h2>Deuxieme groupe</h2>\n<p>Radical de l'indicatif present + iss + e, es, e, ions, iez, ent.</p>\n<h3>Finir</h3>\n<ul>\n<li>Que je finisse</li>\n<li>Que tu finisses</li>\n<li>Qu'il / elle finisse</li>\n<li>Que nous finissions</li>\n<li>Que vous finissiez</li>\n<li>Qu'ils / elles finissent</li>\n</ul>\n<h3>Choisir</h3>\n<ul>\n<li>Que je choisisse</li>\n<li>Que tu choisisses</li>\n<li>Qu'il / elle choisisse</li>\n<li>Que nous choisissions</li>\n<li>Que vous choisissiez</li>\n<li>Qu'ils / elles choisissent</li>\n</ul>\n<h3>Partir</h3>\n<ul>\n<li>Que je parte</li>\n<li>Que tu partes</li>\n<li>Qu'il / elle parte</li>\n<li>Que nous partions</li>\n<li>Que vous partiez</li>\n<li>Qu'ils / elles partent</li>\n</ul>\n<h2>Troisieme Groupe</h2>\n<p>Le meme cas que le premier. Radical + e, es, e, ions, iez, ent.</p>\n<h3>Couvrir</h3>\n<ul>\n<li>Que je couvre</li>\n<li>Que tu couvres</li>\n<li>Qu'il / elle couvre</li>\n<li>Que nous couvrions</li>\n<li>Que vous couvriez</li>\n<li>Qu'ils / elles couvrent</li>\n</ul>\n<h3>Metre</h3>\n<ul>\n<li>Que je mette</li>\n<li>Que tu mettes</li>\n<li>Qu'il / elle mette</li>\n<li>Qui nous metions</li>\n<li>Que vous metiez</li>\n<li>Qu'ils / elles mettent</li>\n</ul>\n<h3>Dormir</h3>\n<ul>\n<li>Que je dorme</li>\n<li>Que tu dormes</li>\n<li>Qu'il / elle dorme</li>\n<li>Que nous dormions</li>\n<li>Que vous dormiez</li>\n<li>Qu'ils / elles dorment</li>\n</ul>","frontmatter":{"date":null,"path":"/first222","title":"[Français] Le Subjonctif Présent"}}},"pathContext":{}}

